import { Component, h, Prop, Event, EventEmitter } from '@stencil/core';
import { IRecipient } from '../../types/Recipient';
import { IRole } from '../../types/Role';

@Component({
    tag: 'recipient-item-added',
    shadow: true,
    styleUrl: 'recipient-item-added.scss',
})
export class RecipientItemAdded {
    @Prop() recipient: IRecipient;
    @Prop() isSigning: boolean;
    @Event() changeRecipientRole: EventEmitter<IRecipient>;
    @Event() removeRecipient: EventEmitter<IRecipient>;

    private roles: IRole[] = [];

    constructor() {
        this.handleChangeRole = this.handleChangeRole.bind(this);
        this.handleRemoveRecipient = this.handleRemoveRecipient.bind(this);
        this.selectedRole = this.selectedRole.bind(this);
    }

    public componentWillLoad() {
        this.addRecipientRoles();
    }

    addRecipientRoles() {
        if (this.isSigning) {
            this.roles.push({
                value: 'signer',
                label: 'Signer',
            });
        }

        this.roles.push(
            {
                value: 'cc',
                label: 'Viewer',
            },
            {
                value: 'approver',
                label: 'Internal approver',
            },
            {
                value: 'externalApprover',
                label: 'External approver',
            }
        );

        if (!this.recipient.role) {
            this.recipient.role = this.roles[0].value;
            this.changeRecipientRole.emit(this.recipient as IRecipient);
        }
    }

    public render() {
        const { fullname, email } = this.recipient;

        return (
            <li class="recipient-list-item">
                <div class="recipient-info-container">
                    <span>{fullname}</span>
                    <div class="recipient-info-contact-data">
                        <span>{email}</span>
                    </div>
                </div>
                <div class="recipient-role-container">
                    <select
                        class="recipient-role-list"
                        onInput={event => this.handleChangeRole(event)}
                    >
                        {this.roles.map(role => {
                            return (
                                <option
                                    value={role.value}
                                    selected={this.selectedRole(role)}
                                >
                                    {role.label}
                                </option>
                            );
                        })}
                    </select>
                </div>
                <div
                    class="recipient-remove-button"
                    onClick={this.handleRemoveRecipient}
                >
                    <limel-icon name="trash" size="small" />
                </div>
            </li>
        );
    }

    private handleChangeRole(event) {
        this.recipient.role = event.target.value;
        this.changeRecipientRole.emit(this.recipient as IRecipient);
    }

    private handleRemoveRecipient() {
        this.removeRecipient.emit(this.recipient as IRecipient);
    }

    private selectedRole(role): boolean {
        return this.recipient.role === role.value;
    }
}
