import { Component, h, Prop } from '@stencil/core';
import { IRecipient } from '../../types/Recipient';
import { IDocument } from '../../types/Document';

@Component({
    tag: 'selected-recipient-list',
    shadow: true,
    styleUrl: 'selected-recipient-list.scss',
})
export class SelectedRecipientList {
    @Prop() public recipients: IRecipient[];
    @Prop() public document: IDocument;

    public render() {
        if (!this.recipients.length) {
            return (
                <empty-state
                    icon="user"
                    text="No recipients added. Find and add recipients to the left!"
                />
            );
        }

        return (
            <ul class="recipient-list">
                {this.recipients.map(selectedRecipient => {
                    return (
                        <recipient-item-added
                            recipient={selectedRecipient}
                            isSigning={this.document.is_signing}
                        />
                    );
                })}
            </ul>
        );
    }
}
