@import '../../gacolors.scss';

.recipient-list-item {
    display: flex;
    align-items: center;
    padding: 0.5rem;
    cursor: pointer;
    border-bottom: 1px solid $ga-light-gray;

    &:hover {
        background-color: rgb(var(--contrast-300));
    }

    .recipient-icon {
        display: flex;
        align-items: center;
        margin-right: 1rem;
        padding: 0.5em;
        border-radius: 50%;
        background-color: $ga-blue;
    }

    .recipient-info-container {
        display: flex;
        flex-direction: column;
        flex-grow: 2;
        font-size: 0.7rem;

        .recipient-info-contact-data {
            display: flex;
            flex-wrap: wrap;
            overflow: hidden;
        }
    }

    .recipient-role-container {
        padding: 0.5rem 1rem;

        .recipient-role-list {
            padding: 0.5rem;
            border: none;
            background-color: transparent;
            outline: none;
            color: $lime-main-color;
        }
    }

    .recipient-remove-button {
        display: flex;
        color: $ga-red;
    }
}
